import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'start',
    redirect: '/login',
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('../components/login/LoginComponent.vue')
  },
  {
    path: '/main',
    component: () => import('../views/HomeView.vue'),
    redirect: '/main/p3',//重定向，当访问/main时，直接重定向到 /main/p1
    //嵌套路由（子路由），对应的组件会展示在当前组件内部
    children:[
      {
        path:'/main/p1',
        component: () => import('../views/main/P1View.vue')
      },
      {
        path:'/main/p2',
        component: () => import('../views/main/P2View.vue')
      },
      {
        path:'/main/p3',
        component: () => import('../views/main/P3View.vue')
      },
    ]
  },
  {
    path: '/404',
    component: () => import('../views/404View.vue')
  },
  {
    path: '*',
    redirect: '/404'
  }
]

const router = new VueRouter({
  routes
})

export default router
